.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
}

.navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.navbar-links_logo {
  margin-right: 2rem;
}

.navbar-links_logo img {
  height: 60px;
  width: 120px;
}

.navbar-links_container {
  display: flex;
  flex-direction: row;
}

.navbar-sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.navbar-links_container p,
.navbar-sign p,
.navbar-menu_container p {
  cursor: pointer;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 500;
  line-height: 25px;
  margin: 0 1rem;
  text-transform: capitalize;
}

.navbar-sign button,
.navbar-menu_container button {
  background: #02132e;
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 500px;
  left: 1153px;
  line-height: 25px;
  outline: none;
  padding: 0.5rem 1rem;
}

.navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;
}

.navbar-menu svg {
  cursor: pointer;
}

.navbar-menu_container {
  align-items: flex-end;
  background: var(--color-footer);
  border-radius: 5px;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-top: 1rem;
  min-width: 210px;
  padding: 2rem;
  position: absolute;
  right: 0;
  text-align: end;
  top: 40px;
}

.navbar-menu_container p {
  color: #fff;
  margin: 1rem 0;
}

.navbar-menu_container-links-sign {
  display: none;
}

@media screen and (max-width: 1050px) {
  .navbar-links_container {
    display: none;
  }
  .navbar-menu {
    display: flex;
  }
}

@media screen and (max-width: 800px) {
  .navbar {
    padding: 2rem 4rem;
  }
}

@media screen and (max-width: 650px) {
  .navbar {
    padding: 2rem;
  }

  .navbar-sign {
    display: none;
  }

  .navbar-menu_container {
    top: 20px;
  }

  .navbar-menu_container-links-sign {
      display: block;
  }
}
