.footer {
  display: flex;
  color: var(--color-text);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--color-footer);
}

.footer-heading {
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
}

.footer-heading h1 {
  font-family: var(--font-family);
  font-size: 42px;
  font-weight: 800px;
  letter-spacing: -0.04em;
  line-height: 52px;
}

.footer-btn {
  align-items: center;
  background: #02132e;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  display: flex;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 500px;
  justify-content: center;
  line-height: 25px;
  margin-bottom: 10rem;
  padding: 0.5rem 1rem;
}

.footer-links_logo {
  display: flex;
  flex-direction: column;
  margin-right: 2rem;
}

.footer-links_logo img {
  height: 120px;
  width: 240px;
}

.footer-links {
  align-items: flex-start;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  text-align: left;
  width: 100%;
}

.footer-links div {
  margin: 1rem;
  width: 250px;
}

.footer-links_div {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.footer-links_div h4 {
  font-family: var(--font-family);
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
}

.footer-links_div p {
  color: var(--color-bg);
  cursor: pointer;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 500;
  line-height: 25px;
  margin: 0.5rem 0;
}

.footer-copyright {
  margin-top: 2rem;
  text-align: center;
  width: 100%;
}

.footer-copyright p {
  color: var(--color-bg);
  cursor: pointer;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 500;
  line-height: 25px;
}

@media screen and (max-width: 850px) {
  .footer-heading h1 {
    font-size: 34px;
    line-height: 28px;
  }
}

@media screen and (max-width: 550px) {
  .footer-heading h1 {
    font-size: 28px;
    line-height: 34px;
  }

  .footer-links_div {
    margin: 1rem 0;
  }
  .footer-heading-btn p {
    font-size: 16px;
    line-height: 20px;
  }
}

@media screen and (max-width: 550px) {
    .footer-heading h1 {
      font-size: 28px;
      line-height: 34px;
    }
  }

