.reviews {
    display: flex;
    flex-direction: row;
}

.reviews-image {
    align-items: center;
    display: flex;
    flex: 1;
    justify-content: flex-start;
    margin-right: 2rem;
}

.reviews-image img {
    max-height: 600px;
    max-width: 100%;
}

.reviews-content {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: flex-end;
}

.reviews-content h4 {
    color: var(--color-subtext);
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
}

.reviews-content h1{
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 900;
    line-height: 40px;
    margin: 1rem 0;
}
.reviews-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
    margin-bottom: 2rem;
}

.reviews-content h4:last-child {
    color: var(--color-subtext);
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
}

@media screen and (max-width: 1000px) {
    .reviews {
        flex-direction: column;
    }
    .reviews-image {
        margin: 1rem 0;
    }

    .reviews-content {
        margin-top: 2rem;
    }
}